html {
	box-sizing: border-box;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

body {
	min-width: 320px;
	font-family: "Roboto", sans-serif;
	font-weight: 400;
	color: #494949;
}

body.lock {
	overflow: hidden;
}

input {
	border: none;

	&::placeholder {
		opacity: 1;
	}
}

textarea {
	border: none;
	resize: none;

	&::placeholder {
		opacity: 1;
	}
}

img {
	max-width: 100%;
}

a {
	text-decoration: none;
}

ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

button {
	border: none;
	padding: 0;
	background: transparent;
	cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
	margin: 0;
}

p {
	line-height: 160%;
}

.wrapper {
	position: relative;
	width: 100%;
	min-height: 100%;
	overflow: hidden;
	display: flex;
	flex-direction: column;
}

.main {
	flex: 1 1 auto;
}

.container {
	margin: 0 auto;
	padding: 0 15px;
	max-width: 1145px;

	@media (max-width: 1145px) {
		max-width: 970px;
	}

	@media (max-width: 991px) {
		max-width: 750px;
	}

	@media (max-width: 767px) {
		max-width: none;
	}
}

.title {
	font-family: "Raleway", sans-serif;
	font-size: 45px;
	line-height: 110.4%;
	color: #232323;

	@media (max-width: 575px) {
		font-size: 30px;
	}
}

.btn {
	position: relative;
	overflow: hidden;
	display: inline-block;
	text-transform: uppercase;
	font-family: "Raleway", sans-serif;
	border-radius: 8px;
	transition: all 0.3s ease-out;
	border: 2px solid transparent;
	letter-spacing: 0.105em;

	transform: perspective(1px) translateZ(0);
	transition-property: color;
	transition-duration: 0.3s;

	&::before {
		content: "";
		position: absolute;
		z-index: -1;
		border-radius: 6px;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		transform: scaleX(0);
		transform-origin: 50%;
		transition-property: transform;
		transition-duration: 0.3s;
		transition-timing-function: ease-out;
	}

	&:hover:before {
		transform: scaleX(1);
	}

	&--stroke {
		border-color: #6052FF;
		color: #494949;
		padding: 15px 56px;
		font-size: 12px;
		line-height: 14px;

		&:before {
			background-color: #6052FF;
		}

		&:hover {
			color: #fff;
		}
	}

	&--fill {
		background-color: #6052FF;
		color: #fff;
		padding: 25px 73px;
		font-size: 14px;

		@media (max-width: 1145px) {
			padding: 15px 30px;
		}

		&:before {
			background-color: #fff;
		}

		&:hover {
			border-color: #6052FF;
			color: #494949;
		}
	}
}

.info {
	@include jcc;

	&--column {
		flex-direction: column;
	}

	&__link {
		position: relative;
		color: #232323;
		padding-left: 35px;
		transition: all 0.3s ease;

		&:hover {
			color: #6052ff;
		}

		&::before {
			content: '';
			background-repeat: no-repeat;
			background-position: center;
			background-size: 13px;
			position: absolute;
			top: 50%;
			left: 0;
			transform: translateY(-50%);
			width: 24px;
			height: 24px;
			border-radius: 5px;
		}

		&--tel {
			font-family: "Raleway", sans-serif;
			font-size: 15px;
			line-height: 18px;

			&:hover {
				color: #B2ADE7;
			}

			&::before {
				background-image: url('../img/svg/tel.svg');
				background-color: #DFDCFF;
			}
		}

		&--mail {
			font-size: 14px;
			line-height: 16px;

			&:hover {
				color: #6DD171;
			}

			&::before {
				background-image: url('../img/svg/mail.svg');
				background-color: #CBFFCD;
			}
		}
	}

	&__link+&__link {
		margin-top: 14px;
	}
}

.socials {
	position: relative;
	z-index: 1;
	@include jcc;
	margin-top: 20px;

	&__desc {
		font-size: 13px;
		line-height: 15px;
		color: #232323;
		margin-right: 10px;
	}

	&__links {
		display: flex;
	}

	&__link {
		display: inline-block;
		width: 24px;
		height: 24px;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			opacity: 1;
			transition: all 0.3s ease;

			&:hover {
				opacity: 0.7;
			}
		}
	}

	&__link+&__link {
		margin-left: 5px;
	}
}

.swiper__wrap {
	position: relative;
	max-width: 1300px;
	margin: auto;
}

.swiper__inner {
	margin: 0 auto;
	padding: 0 15px;
	max-width: 1145px;
}

.swiper__inner .swiper {
	padding-bottom: 50px;
}

.swiper__buttons .swiper-button-next::after,
.swiper__buttons .swiper-button-prev::after {
	content: "";
}

.swiper__buttons .swiper-button-prev,
.swiper__buttons .swiper-button-next {
	background-size: 27px;
	background-position: center;
	background-repeat: no-repeat;
	transition: all 0.3s ease;

	&:hover {
		opacity: 0.8;
	}

	@media (max-width: 1210px) {
		display: none;
	}
}

.swiper__buttons .swiper-button-prev {
	background-image: url("../img/svg/arrow-left.svg");
}

.swiper__buttons .swiper-button-next {
	background-image: url("../img/svg/arrow-right.svg");
}

.swiper .swiper-pagination-bullet {
	width: 10px;
	height: 10px;
}

.swiper .swiper-pagination-bullet-active {
	background: #9E99E0;
}

#up {
	display: none;
	position: fixed;
	bottom: 30px;
	right: 30px;
	cursor: pointer;
	z-index: 1;
	opacity: 0.5;
	transition: all 0.3s ease;

	@media (max-width: 575px) {
		bottom: 10px;
		right: 10px;
	}

	&:hover {
		opacity: 1;
	}
}

.btn:disabled {
	opacity: 0.5;
	-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=50)";
	cursor: default;
	color: #fff;

	&:before,
	&:hover:before {
		background-color: #6052ff;
	}
}