.advantage {
	padding-top: 77px;
	background: linear-gradient(0deg, rgba(255, 255, 255, 1) 33%, rgba(239, 237, 253, 1) 53%, rgba(255, 255, 255, 1) 94%);

	@media (max-width: 991px) {
		padding-top: 60px;
	}

	&__content {
		display: flex;
		justify-content: space-between;

		@media (max-width: 1360px) {
			align-items: center;
		}

		@media (max-width: 991px) {
			flex-direction: column;
		}
	}

	&__img {
		width: 736px;
		height: 449px;
		margin-left: -110px;

		@media (max-width: 1360px) {
			width: 519px;
			height: 317px;
			margin-left: 0;
		}

		@media (max-width: 991px) {
			margin: auto;
			width: 100%;
			height: 100%;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__info {
		.title {
			@media (min-width: 1145px) {
				max-width: 380px;
			}
		}

		@media (min-width: 992px) {
			flex: 0 0 40%;
		}

		@media (max-width: 991px) {
			margin-top: 40px;
		}
	}

	&__list {
		margin-top: 39px;

		@media (max-width: 991px) {
			display: grid;
			grid-template-columns: repeat(2, 1fr);
			grid-gap: 40px;
		}

		@media (max-width: 767px) {
			grid-template-columns: repeat(1, 1fr);
		}

		li {
			position: relative;
			font-size: 17px;
			padding-left: 57px;
			cursor: default;

			@media (max-width: 767px) {
				font-size: 15px;
			}

			&::before {
				content: '';
				background-image: url('../img/svg/check-1.svg');
				background-size: 15px;
				background-position: center;
				background-repeat: no-repeat;
				position: absolute;
				top: 50%;
				left: 0;
				transform: translateY(-50%);
				width: 40px;
				height: 40px;
				background-color: rgba(255, 255, 255, 0.48);
				box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.04);
				border-radius: 50%;
				transition: all 0.3s ease;
			}

			&:hover:before {
				background-image: url('../img/svg/check-2.svg');
				background-color: #6052FF;
			}
		}

		li+li {
			@media (min-width: 992px) {
				margin-top: 30px;
			}
		}
	}

	&__block {
		position: relative;
		display: flex;
		margin-top: 112px;
		background: linear-gradient(101.4deg, #E2F5FA 6.21%, #DBE1FF 58.39%);
		border-radius: 52px;
		padding-top: 35px;

		@media (max-width: 991px) {
			margin-top: 60px;
			padding-top: 0;
			border-radius: 20px;
		}

		&::before {
			@media (min-width: 992px) {
				content: '';
				background-image: url('../img/thumb-3.png');
				background-repeat: no-repeat;
				background-position: center;
				background-size: contain;
				position: absolute;
				right: 30px;
				top: -80px;
				width: 238px;
				height: 103px;
			}

			@media (min-width: 1360px) {
				top: -66px;
				right: -77px;
			}
		}
	}

	&__text {
		position: relative;
		padding: 43px 20px 0 85px;

		@media (min-width: 992px) {
			flex: 0 0 60%;
		}

		@media (max-width: 991px) {
			padding: 60px 35px;
		}

		@media (max-width: 575px) {
			padding: 60px 20px;
		}

		&::before {
			content: '';
			background-image: url('../img/svg/quotes.svg');
			background-repeat: no-repeat;
			background-position: center;
			background-size: contain;
			position: absolute;
			top: 30px;
			left: 98px;
			width: 98px;
			height: 70px;
			z-index: 1;

			@media (max-width: 991px) {
				left: 60px;
			}
		}

		p {
			position: relative;
			z-index: 1;
		}

		p:first-child {
			font-size: 20px;
			line-height: 160%;

			@media (max-width: 991px) {
				font-size: 18px;
			}
		}

		p:last-child {
			@media (max-width: 991px) {
				font-size: 14px;
			}
		}

		p+p {
			margin-top: 20px;
		}
	}

	&__pic {
		position: relative;
		width: 380px;
		height: 369px;

		&::before {
			content: '';
			position: absolute;
			bottom: 20px;
			right: -9px;
			width: 312px;
			height: 312px;
			background: rgba(255, 255, 255, 0.45);
			border-radius: 50%;
		}

		img {
			position: relative;
			z-index: 1;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		@media (max-width: 991px) {
			display: none;
		}
	}
}