.portfolio {
	margin-top: 83px;

	&__swiper {
		margin-top: 45px;
	}

	&__item {
		background: #F7F6FE;
		border-radius: 17px;
	}

	&__img {
		display: inline-block;
		width: 100%;
		height: 260px;
		overflow: hidden;
		border-radius: 17px;

		@media (max-width: 575px) {
			height: auto;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			transition: all 0.3s ease;

			&:hover {
				transform: scale(1.1);
			}
		}
	}

	&__body {
		padding: 30px 30px 61px;
	}

	&__subtitle {
		font-family: "Raleway", sans-serif;
		font-size: 26px;
		line-height: 110.4%;
		color: #232323;

		@media (max-width: 767px) {
			font-size: 20px;
		}
	}

	&__text {
		margin-top: 10px;

		@media (max-width: 767px) {
			font-size: 14px;
		}
	}
}