.seo {
	margin-top: 126px;
	padding: 140px 0 112px;
	background: linear-gradient(0deg, rgba(246, 234, 252, 1) 33%, rgba(233, 235, 252, 1) 53%, rgba(233, 235, 252, 1) 94%);

	@media (max-width: 991px) {
		padding: 50px 0;
		margin-top: 40px;
	}

	&__content {
		display: flex;
		justify-content: space-between;

		@media (max-width: 1145px) {
			align-items: center;
		}

		@media (max-width: 991px) {
			flex-direction: column;
		}
	}

	&__img {
		position: relative;
		width: 649px;
		height: 402px;
		margin-left: -116px;

		@media (max-width: 1325px) {
			width: 549px;
			height: 345px;
			margin-left: 0;
		}

		@media (max-width: 1145px) {
			width: 449px;
			height: 270px;
		}

		@media (max-width: 991px) {
			width: 100%;
			height: 100%;
		}

		&::before {
			@media (min-width: 992px) {
				content: '';
				background-image: url('../img/thumb-2.png');
				background-repeat: no-repeat;
				background-position: center;
				background-size: contain;
				position: absolute;
				top: -37px;
				right: 34px;
				width: 176px;
				height: 106px;
			}
		}

		img {
			position: relative;
			z-index: 1;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__info {
		@media (min-width: 992px) {
			flex: 0 0 49%;
		}

		@media (max-width: 991px) {
			margin-top: 30px;
		}
	}

	&__text {
		margin-top: 40px;

		@media (max-width: 991px) {
			font-size: 14px;
			margin-top: 20px;
		}
	}

	&__btn {
		margin-top: 35px;
		padding-left: 41px;
		padding-right: 41px;

		@media (max-width: 1145px) {
			padding-left: 30px;
			padding-right: 30px;
		}

		@media (max-width: 575px) {
			width: 100%;
			text-align: center;
		}
	}
}