.header {
	position: absolute;
	width: 100%;
	top: -150px;
	left: 0;
	z-index: 1;
	transition: top 1s ease;

	&__content {
		@include jcsb;
	}

	&__logo {
		display: flex;
		position: relative;
		z-index: 3;
	}

	&__btn {
		@media (max-width: 991px) {
			display: none;
		}
	}
}

.loaded .header {
	top: 35px;
}

.menu {
	@media (min-width: 1145px) {
		margin-left: 50px;
	}

	&__body {
		@media (max-width: 991px) {
			background-color: #fff;
			position: fixed;
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
			overflow: auto;
			padding: 110px 20px 20px;
			opacity: 0;
			visibility: hidden;
			transition: all .3s ease 0s;
			text-align: center;
			z-index: 2;

			&:before {
				content: "";
				background-color: #fff;
				position: fixed;
				top: 0;
				left: 0;
				width: 100%;
				height: 90px;
				z-index: 50;
			}

			&.active {
				opacity: 1;
				visibility: visible;
			}
		}

		&.active {
			@media (max-width: 991px) {
				.menu__list {
					opacity: 1;
					visibility: visible;
				}
			}
		}
	}


	&__list {
		display: flex;

		@media (max-width: 991px) {
			flex-direction: column;
			margin-bottom: 30px;
		}

		&>li {
			@media (max-width: 991px) {
				border-top: 1px solid #F2F2F2;

				&:last-child {
					border-bottom: 1px solid #F2F2F2;
				}
			}

			&>a {
				position: relative;
				font-size: 15px;
				line-height: 18px;
				color: #494949;

				@media (min-width: 992px) {
					padding-bottom: 5px;
					transition: color .1s ease-in;

					&::before {
						content: "";
						position: absolute;
						left: 51%;
						right: 51%;
						bottom: 0;
						background: #6052FF;
						height: 2px;
						transition-property: left, right;
						transition-duration: .3s;
						transition-timing-function: ease-out;
					}

					&:hover:before {
						left: 0;
						right: 0;
					}

					&:hover {
						color: #6052FF;
					}
				}

				@media (max-width: 991px) {
					padding: 20px 0;
					display: block;
				}
			}
		}

		&>li+li {
			margin-left: 65px;

			@media (max-width: 1145px) {
				margin-left: 30px;
			}

			@media (max-width: 991px) {
				margin-left: 0;
			}
		}
	}

	&__info {
		&--mob {
			display: none;

			@media (max-width: 991px) {
				display: block;
			}

			.socials__link {
				width: 33px;
				height: 33px;
			}

			.socials__link+.socials__link {
				margin-left: 35px;
			}
		}
	}
}

@media (max-width: 991px) {
	.menu__icon {
		z-index: 5;
		display: block;
		position: relative;
		width: 35px;
		height: 20px;
		cursor: pointer;
	}

	.menu__icon span,
	.menu__icon::before,
	.menu__icon::after {
		left: 0;
		position: absolute;
		height: 10%;
		width: 100%;
		transition: all 0.3s ease 0s;
		background-color: #232323;
	}

	.menu__icon::before,
	.menu__icon::after {
		content: "";
	}

	.menu__icon::before {
		top: 0;
	}

	.menu__icon::after {
		bottom: 0;
	}

	.menu__icon span {
		top: 50%;
		transform: scale(1) translate(0px, -50%);
	}

	.menu__icon.active span {
		transform: scale(0) translate(0px, -50%);
	}

	.menu__icon.active::before {
		top: 50%;
		transform: rotate(-45deg) translate(0px, -50%);
	}

	.menu__icon.active::after {
		bottom: 50%;
		transform: rotate(45deg) translate(0px, 50%);
	}

