  background: linear-gradient(0deg, #ebe8fc 33%, #ebe8fc 53%, white 94%); }
  @media (max-width: 991px) {
    .application {
      padding: 60px 0 50px; } }
  .application__content {
    position: relative;
    display: flex;
    justify-content: flex-end; }
  .application__img {
    width: 854px;
    height: 723px;
    position: absolute;
    left: -94px;
    top: -68px; }
    .application__img img {
      width: 100%;
      height: 100%;
      object-fit: cover; }
    @media (max-width: 991px) {
      .application__img {
        display: none; } }
  .application__item {
    position: relative;
    display: flex;
    flex-direction: column;
    border-radius: 39px;
    background: rgba(255, 255, 255, 0.76);
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1); }
    @media (min-width: 992px) {
      .application__item {
        flex: 0 0 50%; } }
    @media (min-width: 1146px) {
      .application__item {
        flex: 0 0 45.4%; } }
    @media (max-width: 991px) {
      .application__item {
        padding: 30px; } }
    @media (max-width: 575px) {
      .application__item {
        padding-left: 15px;
        padding-right: 15px; } }
  .application__caption {
    position: relative;
    z-index: 1; }
    @media (min-width: 992px) {
      .application__caption {
        padding: 55px 25px 0; } }
  .application__title {
    font-family: "Raleway", sans-serif;
    font-size: 26px;
    line-height: 110.4%;
    text-align: center;
    color: #232323; }
    @media (max-width: 575px) {
      .application__title {
        font-size: 20px; } }
  .application__text {
    margin-top: 12px;
    font-size: 18px;
    text-align: center; }
    @media (max-width: 575px) {
      .application__text {
        font-size: 14px; } }
  .application__form {
    display: flex;
    flex-direction: column;
    position: relative;
    z-index: 1; }
    @media (min-width: 992px) {
      .application__form {
        padding: 0 70px 60px;
        margin-top: 3px; } }
    @media (max-width: 991px) {
      .application__form {
        margin-top: 20px; } }
    .application__form input {
      border-bottom: 1px solid #E2E2E2;
      padding: 23px 3px 17px;
      background-color: transparent; }
      .application__form input::placeholder {
        color: #ABABAB; }
        @media (max-width: 575px) {
          .application__form input::placeholder {
            font-size: 14px; } }
      .application__form input:focus {
        outline: none; }
    .application__form textarea {
      height: 85px;
      border-bottom: 1px solid #E2E2E2;
      margin-top: 20px;
      padding: 8px 3px;
      background-color: transparent; }
      .application__form textarea::placeholder {
        color: #ABABAB; }
        @media (max-width: 575px) {
          .application__form textarea::placeholder {
            font-size: 14px; } }
      .application__form textarea:focus {
        outline: none; }
    .application__form label {
      display: flex;
      align-items: center;
      margin-top: 20px;
      font-size: 13px;
      cursor: pointer; }
      @media (max-width: 991px) {
        .application__form label {
          display: flex;
          justify-content: center;
          margin-top: 30px; } }
      .application__form label input {
        display: none; }
      .application__form label input:checked + span {
        background-image: url("../img/svg/check-1.svg"); }
      .application__form label span {
        width: 100%;
        max-width: 22px;
        height: 22px;
        background: #EAEAEA;
        border-radius: 4px;
        background-size: 16px;
        background-position: center;
        background-repeat: no-repeat; }
      .application__form label i {
        display: inline-block;
        padding-left: 20px;
        font-style: normal; }
        @media (max-width: 575px) {
          .application__form label i {
            padding-left: 10px; } }
      .application__form label a {
        color: #6052FF; }
        .application__form label a:hover {
          text-decoration: underline; }
  .application__btn {
    margin-top: 44px; }
  @media (max-width: 991px) {
    .application__popup {
      padding: 60px; } }
  @media (max-width: 575px) {
    .application__popup {
      padding-left: 15px;
      padding-right: 15px; } }

.footer {
