.application {
	padding: 117px 0 129px;
	background: linear-gradient(0deg, rgba(235, 232, 252, 1) 33%, rgba(235, 232, 252, 1) 53%, rgba(255, 255, 255, 1) 94%);

	@media (max-width: 991px) {
		padding: 60px 0 50px;
	}

	&__content {
		position: relative;
		display: flex;
		justify-content: flex-end;
	}

	&__img {
		width: 854px;
		height: 723px;
		position: absolute;
		left: -94px;
		top: -68px;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		@media (max-width: 991px) {
			display: none;
		}
	}

	&__item {
		position: relative;
		display: flex;
		flex-direction: column;
		border-radius: 39px;
		background: rgba(255, 255, 255, 0.76);
		box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);

		@media (min-width: 992px) {
			flex: 0 0 50%;
		}

		@media (min-width: 1146px) {
			flex: 0 0 45.4%;
		}


		@media (max-width: 991px) {
			padding: 30px;
		}

		@media (max-width: 575px) {
			padding-left: 15px;
			padding-right: 15px;
		}
	}

	&__caption {
		position: relative;
		z-index: 1;

		@media (min-width: 992px) {
			padding: 55px 25px 0;
		}
	}

	&__title {
		font-family: "Raleway", sans-serif;
		font-size: 26px;
		line-height: 110.4%;
		text-align: center;
		color: #232323;

		@media (max-width: 575px) {
			font-size: 20px;
		}
	}

	&__text {
		margin-top: 12px;
		font-size: 18px;
		text-align: center;

		@media (max-width: 575px) {
			font-size: 14px;
		}
	}

	&__form {
		display: flex;
		flex-direction: column;
		position: relative;
		z-index: 1;

		@media (min-width: 992px) {
			padding: 0 70px 60px;
			margin-top: 3px;
		}

		@media (max-width: 991px) {
			margin-top: 20px;
		}

		input {
			border-bottom: 1px solid #E2E2E2;
			padding: 23px 3px 17px;
			background-color: transparent;

			&::placeholder {
				color: #ABABAB;

				@media (max-width: 575px) {
					font-size: 14px;
				}
			}

			&:focus {
				outline: none;
			}
		}

		textarea {
			height: 85px;
			border-bottom: 1px solid #E2E2E2;
			margin-top: 20px;
			padding: 8px 3px;
			background-color: transparent;

			&::placeholder {
				color: #ABABAB;

				@media (max-width: 575px) {
					font-size: 14px;
				}
			}

			&:focus {
				outline: none;
			}
		}

		label {
			display: flex;
			align-items: center;
			margin-top: 20px;
			font-size: 13px;
			cursor: pointer;

			@media (max-width: 991px) {
				display: flex;
				justify-content: center;
				margin-top: 30px;
			}

			input {
				display: none;
			}

			input:checked+span {
				background-image: url('../img/svg/check-1.svg');

			}

			span {
				width: 100%;
				max-width: 22px;
				height: 22px;
				background: #EAEAEA;
				border-radius: 4px;
				background-size: 16px;
				background-position: center;
				background-repeat: no-repeat;
			}

			i {
				display: inline-block;
				padding-left: 20px;
				font-style: normal;

				@media (max-width: 575px) {
					padding-left: 10px;
				}
			}

			a {
				color: #6052FF;

				&:hover {
					text-decoration: underline;
				}
			}
		}
	}

	&__btn {
		margin-top: 44px;
	}

	&__popup {
		@media (max-width: 991px) {
			padding: 60px;
		}

		@media (max-width: 575px) {
			padding-left: 15px;
			padding-right: 15px;
		}
	}
}