.intro {
	padding: 146px 0 90px;
	background: linear-gradient(0deg, rgba(255, 255, 255, 1) 33%, rgba(235, 232, 252, 1) 53%, rgba(235, 232, 252, 1) 94%);


	@media (max-width: 991px) {
		padding-top: 125px;
	}

	&__content {
		display: flex;
		justify-content: space-between;

		@media (max-width: 991px) {
			flex-direction: column;
		}
	}

	&__img {
		position: relative;
		width: 785px;
		height: 492px;
		margin: 46px 0 0 -143px;

		@media (max-width: 1325px) {
			width: 685px;
			height: 415px;
			margin: 46px 0 0 -50px;
		}

		@media (max-width: 1145px) {
			width: 585px;
			height: 345px;
			margin: 46px 0 0 0;
		}

		@media (max-width: 991px) {
			width: 100%;
			height: 100%;
			margin: 0;
		}

		&::before {
			@media (min-width: 992px) {
				content: '';
				background-image: url('../img/thumb-1.png');
				background-repeat: no-repeat;
				background-position: center;
				background-size: cover;
				position: absolute;
				top: -58px;
				left: 81px;
				width: 148px;
				height: 133px;
			}
		}

		img {
			position: relative;
			z-index: 1;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__info {
		margin-top: 55px;
		display: flex;
		flex-direction: column;
		align-items: flex-start;

		@media (min-width: 992px) {
			flex: 0 0 41%
		}

		@media (max-width: 991px) {
			align-items: center;
			margin-top: 25px;
		}
	}

	&__title {
		font-family: "Raleway", sans-serif;
		font-size: 66px;
		line-height: 101.9%;
		min-height: 211px;

		@media (max-width: 1145px) {
			font-size: 34px;
			max-width: 280px;
			min-height: 104px;
		}

		@media (max-width: 991px) {
			text-align: center;
		}

		p {
			display: inline-block;
			color: #6052FF;
			line-height: 1;
		}

		i {
			font-style: normal;
		}
	}

	&__desc {
		margin-top: 8px;
		font-size: 30px;
		line-height: 35px;

		@media (max-width: 1145px) {
			font-size: 18px;
		}

		@media (max-width: 991px) {
			text-align: center;
		}
	}

	&__text {
		position: relative;
		display: inline-block;
		margin-top: 37px;
		font-size: 18px;
		line-height: 21px;

		&::before {
			@media (min-width: 576px) {
				content: '';
				background-image: url('../img/svg/arrow-down.svg');
				background-repeat: no-repeat;
				background-position: center;
				background-size: cover;
				position: absolute;
				top: 7px;
				right: -68px;
				width: 27px;
				height: 58px;
			}

			@media (max-width: 1145px) {
				right: -50px;
			}
		}

		@media (max-width: 1145px) {
			margin-top: 10px;
			font-size: 15px;
		}
	}

	&__btn {
		margin-top: 16px;

		&--animate {
			height: 250px;
			width: 250px;
			position: absolute;
			top: -20px;
			left: -120px;
			background: rgba(255, 255, 255, 1.0);
			border-radius: 100%;
			transform: scale(0);
			animation: ripple 4s linear infinite;
		}

		@keyframes ripple {
			20% {
				opacity: 0;
				transform: scale(2.5);
			}

			100% {
				opacity: 0;
				transform: scale(2.5);
			}
		}
	}
}