.about-us {

	&__content {
		display: flex;
		justify-content: space-between;

		@media (max-width: 991px) {
			flex-direction: column;
		}
	}

	&__left {
		margin-right: 60px;
		padding-top: 63px;

		@media (max-width: 1145px) {
			margin-right: 35px;
		}

		@media (max-width: 991px) {
			margin-right: 0;
			order: 1;
		}
	}

	&__text {
		@media (max-width: 575px) {
			font-size: 14px;
		}

		p {
			margin-top: 30px;
		}

		strong {
			display: inline-block;
			margin-top: 20px;
			font-family: "Raleway", sans-serif;
			line-height: 160%;

			@media (min-width: 1146px) {
				padding-right: 10px;
			}
		}
	}

	&__right {
		position: relative;
	}

	&__img {
		width: 498px;
		height: 452px;

		@media (max-width: 991px) {
			width: 100%;
			height: 100%;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: 40px;
		}
	}

	&__info {
		position: absolute;
		bottom: 5px;
		left: -137px;
		background: rgba(255, 255, 255, 0.78);
		box-shadow: 0px 12px 15px rgba(0, 0, 0, 0.03);
		border-radius: 15px;

		@media (max-width: 1145px) {
			left: 0;
			bottom: 0;
			width: 100%;
		}

		.info {
			border-bottom: 1px solid #EFEFEF;
			padding: 27px 35px 15px;

			@media (min-width: 1146px) {
				align-items: flex-start;
			}
		}

		.info__link--tel {
			font-size: 18px;
		}

		.info__link--mail {
			font-size: 15px;
		}

		.socials {
			padding: 0 33px 14px;
		}
	}
}