.footer {
	border-top: 1px solid #DFDFDF;
	padding: 70px 0 90px;
	background: linear-gradient(-90deg, rgba(235, 232, 252, 1) 33%, rgba(242, 240, 252, 1) 53%, rgba(235, 232, 252, 1) 94%);

	&__top {
		@include jcsb;

		@media (max-width: 767px) {
			flex-direction: column;
		}

		.info {
			@media (max-width: 767px) {
				flex-direction: column;
				align-items: flex-start;
				margin-top: 30px;
			}

			.info__link {
				@media (min-width: 768px) {
					margin-top: 0;
				}

				@media (max-width: 767px) {
					margin-top: 25px;
				}
			}

			.info__link--tel {
				margin-right: 91px;
				font-size: 18px;

				@media (max-width: 767px) {
					margin: 0 0;
				}
			}

			.info__link--mail {
				font-size: 15px;
			}
		}
	}

	&__title {
		font-family: "Raleway", sans-serif;
		font-size: 26px;
		line-height: 31px;
		color: #232323;
	}

	&__bottom {
		@include jcsb;
		margin-top: 55px;

		@media (max-width: 991px) {
			align-items: flex-start;
		}

		@media (max-width: 767px) {
			flex-direction: column;
			align-items: center;
			text-align: center;
			margin-top: 35px;
		}

		.socials {
			@media (min-width: 768px) {
				margin-top: 0;
			}
		}
	}

	&__text {
		display: flex;
		align-items: center;

		@media (max-width: 991px) {
			flex-direction: column;
			align-items: flex-start;
		}

		@media (max-width: 767px) {
			align-items: center;
		}
	}

	&__copy {
		font-size: 13px;
		color: #ABABAB;
	}

	&__privacy {
		margin-left: 122px;
		font-size: 13px;
		color: #ABABAB;

		@media (max-width: 991px) {
			margin: 10px 0 0 0;
		}

		@media (max-width: 767px) {
			margin-top: 20px;
		}

		&:hover {
			text-decoration: underline;
		}
	}
}