.reviews {
	margin-top: 93px;

	.title {
		text-align: center;
	}

	&__content {
		margin-top: 40px;
	}

	&__item {
		display: flex;
		border: 1px solid #F2F2F8;
		border-radius: 52px;
		padding: 63px 52px;

		@media (max-width: 991px) {
			padding: 30px;
		}

		@media (max-width: 767px) {
			flex-direction: column;
			align-items: center;
		}
	}

	&__img {
		width: 100%;
		max-width: 142px;
		height: 142px;
		background-color: #DCE1FF;
		border-radius: 50%;

		@media (max-width: 991px) {
			max-width: 80px;
			height: 80px;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__info {
		@media (min-width: 768px) {
			margin-left: 38px;
		}
	}

	&__subtitle {
		font-family: "Raleway", sans-serif;
		font-size: 26px;
		line-height: 110.4%;
		color: #232323;

		@media (max-width: 991px) {
			font-size: 20px;
		}

		@media (max-width: 767px) {
			text-align: center;
			margin-top: 25px;
		}
	}

	&__text {
		margin-top: 29px;

		p:first-child {
			font-size: 18px;

			@media (max-width: 991px) {
				font-size: 16px;
			}
		}

		p+p {
			margin-top: 24px;
		}

		p {
			@media (max-width: 991px) {
				font-size: 14px;
			}
		}
	}
}