.services {
	padding-top: 84px;
	background: linear-gradient(0deg, rgba(255, 255, 255, 1) 33%, rgba(246, 245, 254, 1) 53%, rgba(255, 255, 255, 1) 94%);

	&__caption {
		.title {
			text-align: center;
		}
	}

	&__desc {
		margin-top: 20px;
	}

	&__content {
		margin-top: 50px;

		@media (min-width: 1025px) {
			display: grid;
			grid-gap: 24px;
			grid-template-columns: repeat(2, 1fr);
		}
	}

	&__item {
		border: 1px solid #F2F2F8;
		border-radius: 32px;
		padding: 36px 39px;
		background-color: #fff;

		@media (max-width: 991px) {
			padding: 20px;
		}
	}

	&__item--1 {
		.services__icon {
			background-color: #EEFFEE;
		}

		.services__money {
			background-image: url('../img/svg/money-1.svg');
		}

		@media (min-width: 1025px) {
			.services__subtitle {
				max-width: 210px;
			}
		}
	}

	&__item--2 {
		.services__icon {
			background-color: #FFFAEE;
		}

		.services__money {
			background-image: url('../img/svg/money-2.svg');
		}
	}

	&__item--3 {
		.services__icon {
			background-color: #EEFCFF;
		}

		.services__money {
			background-image: url('../img/svg/money-3.svg');
		}

		@media (min-width: 1025px) {
			.services__subtitle {
				max-width: 210px;
			}
		}
	}

	&__item--4 {
		.services__icon {
			background-color: #FFEEFC;
		}

		.services__money {
			background-image: url('../img/svg/money-4.svg');
		}
	}

	&__header {
		display: flex;
		align-items: center;

		@media (max-width: 991px) {
			flex-direction: column;
		}

		@media (max-width: 767px) {
			flex-direction: row;
		}

		@media (max-width: 480px) {
			flex-direction: column;
		}
	}

	&__icon {
		@include jcc;
		width: 100%;
		max-width: 124px;
		height: 124px;
		border-radius: 50%;

		@media (max-width: 1145px) {
			max-width: 105px;
			height: 105px;
		}
	}

	&__subtitle {
		font-family: "Raleway", sans-serif;
		font-size: 26px;
		line-height: 130%;
		color: #232323;
		margin-left: 30px;

		@media (max-width: 991px) {
			margin: 30px 0 0 0;
			text-align: center;
		}

		@media (max-width: 767px) {
			margin: 0 0 0 30px;
			text-align: left;
			font-size: 20px;
		}

		@media (max-width: 480px) {
			margin: 30px 0 0 0;
			text-align: center;
		}
	}

	&__body {
		margin-top: 15px;

		@media (min-width: 1025px) {
			min-height: 130px;
		}
	}

	&__text {
		@media (max-width: 1145px) {
			font-size: 14px;
		}

		@media (max-width: 991px) {
			text-align: center;
		}

		@media (max-width: 767px) {
			text-align: left;
		}
	}

	&__footer {
		@include jcsb;
		border-top: 1px solid #F2F2F8;
		padding-top: 39px;
		margin-top: 27px;

		@media (max-width: 991px) {
			flex-direction: column;
			padding-top: 20px;
		}

		@media (max-width: 767px) {
			flex-direction: row;
		}

		@media (max-width: 480px) {
			flex-direction: column;
		}
	}

	&__money {
		font-family: "Raleway", sans-serif;
		font-size: 20px;
		line-height: 110.4%;
		color: #232323;
		background-size: 20px;
		background-position: left;
		background-repeat: no-repeat;
		padding-left: 35px;
	}

	&__btn {
		padding-left: 61px;
		padding-right: 61px;

		@media (max-width: 1145px) {
			padding-left: 40px;
			padding-right: 40px;
		}

		@media (max-width: 991px) {
			margin-top: 20px;
		}

		@media (max-width: 767px) {
			margin-top: 0;
		}

		@media (max-width: 480px) {
			margin-top: 20px;
		}
	}

	.swiper-initialized {
		padding-bottom: 50px;
	}
}